import { createDoc, fetchDocInfo } from "../api/api";
export default {
  name: "oneTeam",
  data: function data() {
    return {
      webTitle: '团队文档',
      teamName: '',
      chart: '',
      isBuilder: false,
      teamId: ''
    };
  },
  mounted: function mounted() {
    this.init();
  },
  methods: {
    init: function init() {
      var _this = this;

      if (localStorage.getItem('chart')) {
        this.chart = localStorage.getItem('chart');
      } else {
        this.chart = '图标';
      }

      this.teamId = this.$route.params.teamId;
      fetchDocInfo(this.teamId).then(function (res) {
        if (res.status === 200) {
          _this.teamName = res.data.name;
          _this.webTitle = _this.teamName + '的文档';
        }
      }).catch(function (e) {
        return _this.$message({
          message: e.response.data,
          type: 'error'
        });
      });
    },
    toNewDoc: function toNewDoc() {
      var _this2 = this;

      createDoc(0, "未命名", this.teamId).then(function (res) {
        if (res.status === 201) {
          _this2.$message({
            message: '新建文档成功',
            type: 'info'
          });

          _this2.$router.push({
            name: 'editorPage',
            params: {
              docId: res.data.id
            }
          });
        }
      }).catch(function (e) {
        if (e.response.status === 401) {
          _this2.$message({
            message: '您没有权限',
            type: 'error'
          });
        }
      });
    },
    goBack: function goBack() {
      this.$router.push({
        name: 'myTeam'
      });
    },
    changeChart: function changeChart(value) {
      this.chart = value;
      localStorage.setItem('chart', value);
    },
    toTemplate: function toTemplate() {
      this.$router.push({
        name: 'templates',
        params: {
          teamId: this.teamId
        }
      });
    }
  }
};